/* Desktop size*/
.resume{
    display: grid;
    background-color: #da259d;
}
.resume p{
    color: #2a2a2a;
    font-family: Cambria, Cochin, Georgia, Times, 'Times New Roman', serif;
    font-style: oblique;
}
.left-cell{
    background-color: #cf66ac;
}
.right-cell{
    background-color: #ac4c8c; 
}
.div-left{
    padding: 50px;
}
.div-left h1 ,h2,h4,h6{
    color: white;
}
/* Tablet size*/
@media only screen and (min-width: 550px) and (max-width: 1089px){
    .resume{
        background-color: #da259d;
        display: flex;
        flex-direction: column;
        align-items: center;
    }
    .left-cell {
        width: 100%;
    }
}
         /* Mobile Size*/
/*@media only screen and (min-width: 550px) and (max-width: 549px){*/
@media only screen and (min-width: 300px) and (max-width: 549px){
    .resume{
        background-color: #da259d;
        display: flex;
        flex-direction: column;
        align-items: center;
        overflow-x: auto;
        overflow-y: auto;
    }
    .left-cell{
        width: 100%;
    }
    .left-cell h2{
        font-size: 50px;
    }
}