*{
    box-sizing: border-box;
}
html,body{
    margin: 0;
    padding: 0;
}
.image-container{
    margin: 0, auto;
    width: 30%;
    box-shadow: 0 15px 30px rgba(0, 0, 0, 0.30), 0 11px 8px rgba(0, 0, 0, 0.22);
    margin-bottom: 20px;
    transition-duration: 1s;
}
.image-container:hover{
    width: 90%;
    box-shadow: 0 18px 36px rgba(0, 0, 0, 0.30), 0 14px 11px rgba(0, 0, 0, 0.22);
}
.img{
    width: 100%;
    height: auto;
}
.image-description{
    padding: 0 15px 15px 15px;
    text-align: center;
}
.lightbox {
    display: table;
    transition-duration: 1s;
    position: fixed;
    top: -100%;
    left: 0;
    width: 100%;
    height: 100%;
    background: rgba(0, 0, 0, 0.0.7);
}
.lightbox:target{
    top: 0;
}
.lightbox-content{
    display: table-cell;
    vertical-align: middle;
    text-align: center;
}
.close{
    position: absolute;
    top: 10px;
    right: 10px;

    font-size: 20px;
    font-weight: 300;
    text-decoration: none;
    color: #ff0808;
}
.lightbox img{
    max-width: 100%;
    height: auto;
}