.header{
    background: #2703f3;
    align-items: center;
    padding-left: 5em;
    padding-top: 10px;
    height: 20px;
}
.author{
    background: #05596e;
}
.author p{
    justify-content: start;
    text-align: start;
    font-size: large;
    color: aliceblue;
    padding: 1em;
}
.author h4{
    background: #249bb9;
    justify-content: center;
    text-align: center;
    font-size: x-large;
    color: aliceblue;
    padding: 1em;
}
.author-image{
    margin-left: auto;
    margin-right: auto;
    display: block;
    height: 200px;
    width: 200px;
    border: solid white;
    border-radius: 50%;
}
.header p,Button{
    text-align: center;
    justify-content: center;
    color: aliceblue;
    font-size: large;
}
.social-media{
    color: grey;
    display: block;
    margin-left: auto;
    margin-right: auto;
    align-items: center;
    width: 25%;
    padding-bottom: 2em;
}
.social{
    align-content: center;
}
.services{
  background: #05596e;  
}
.services h4{
    background: #249bb9; 
    justify-content: center;
    text-align: center;
    font-size: x-large;
    color: aliceblue;
    padding: 1em;
  }