.slide-container {
    width: auto;
    height: auto;
    padding-bottom: 0%;
    /* margin: auto;  */
  }
   
  h3 {
    text-align: center; }
   
  .each-slide > div {
    display: flex;
    align-items: center;
    justify-content: center;
    background-size: cover;
    height: 300px;
  }
   
  .each-slide span {
    padding: 20px;
    font-size: 20px;
    background: #efefef;
    text-align: center;
  }
   
  .each-fade {
    display: flex;
  }
   
  .each-fade .image-container {
    width: 75%;
    overflow: hidden;
  }
   
  .each-fade .image-container img {
    width: 100%;
  }
   
  .each-fade h2 {
    width: 25%;
    size: 3px;
    display: flex;
    justify-content: center;
    align-items: center;
    margin: 0;
    background: #f50404;
  }

  @media only screen and (min-width: 300px) and (max-width: 549px){
    .each-slide span {
      padding: 20px;
      font-size: 10px;
      background: #efefef;
      text-align: center;
    }
    .slide-container h2{
      font-size: 20px;
      font-family: Arial, Helvetica, sans-serif;
    }
  }