.header{
    background: #05596e;
}
.nav-menu{
    color: aliceblue;
    display: grid;
    align-items: center;
    padding: 30px;
}
.nav-div{
    display: table-row-group;
    flex-direction: column;
    align-items: center;
}
.nav-div link{
    width: 10%;
    font-size: 2px;
    font-weight: bold;
    margin: 20px 0;
    overflow-y: scroll;
}
.what-does{
    background: #05596e;
    text-align: center;
}
.what-does p{
    padding: 20px;
    font-size: 50px;
    color: antiquewhite;
}
.founder-history{
background:#08313b ;
}
.founder-history p{
font-size: xx-large;
line-height: 1.2;
justify-content: start;
text-align: start;
}
.founder-profile{
align-items: center;
justify-content: end;
}
.our-services{
background: #05596e ;
text-align: center;
height: fit-content;
}
.nav-menu a{
    text-decoration: none;
}
.our-services p{
padding: 20px;
font-size: 50px;
color: antiquewhite;
}
.read-more{
background:#177c95 ;
text-align: center;
justify-content: start;
}
.read-more p{
font-size: x-large;
font-weight: bold;
color: antiquewhite;
text-align: center;
justify-content: start;
}
.open-hours{
background: #0c2227 ;
}
.open-hours p{
    color: aliceblue;
    font-size: medium;
    justify-content: start;
    text-align: start;
}
.open-hours Button{
    background: transparent;
    border: solid white;
    border-radius: 10%;
    padding-bottom: 10px;
}
.open-hours Button:hover{
    background: #38bddf;
}
.open-hours h3{
    color: aliceblue;
}
.read-more p{
justify-content: start;
font-size: medium;
text-align: start;
}
.read-more h6{
justify-content: center;
font-size: large;
text-align: center;
color: #615959;
padding-top: 20px;
}