.slide-container {
    width: auto;
    height: auto;
    padding-bottom: 0%;
    /* margin: auto;  */
  }
   
  h3 {
    text-align: center; }
   
  .each-slide > div {
    display: flex;
    align-items: center;
    justify-content: center;
    background-size: cover;
    height: 300px;
  }
   
  .each-slide span {
    padding: 20px;
    font-size: 20px;
    background: #efefef;
    text-align: center;
  }
   
  .each-fade {
    display: flex;
  }
   
  .each-fade .image-container {
    width: 75%;
    overflow: hidden;
  }
   
  .each-fade .image-container img {
