    align-items: center;
    margin: 0;
    background: #f50404;
  }

  @media only screen and (min-width: 300px) and (max-width: 549px){
    .each-slide span {
      padding: 20px;
      font-size: 10px;
      background: #efefef;
      text-align: center;
    }
    .slide-container h2{
      font-size: 20px;
      font-family: Arial, Helvetica, sans-serif;
    }
  }
