.image{
    width: auto;
    height: auto;
    background-size: contain;
}
.image>div{
    background-size: cover;
}
.client{
    align-items: flex-start;
    background: #ffffff;
}
.client h2,p{
    color: #000000;
}
.client-body{
    display: flex;
    column-count: initial;
    align-content: center;
}
@media only screen and (min-width: 300px) and (max-width: 549px){
    .client-body{
        display: flex;
        font-size: smaller;
    }
}