.Author{
    background: #e43b3b;
    text-align: center;
    color: #ffffff;
}

/* Tablet size*/
@media only screen and (min-width: 550px) and (max-width: 1089px){
    .client h4{
        color: #000000;
    }
    .header{
        bottom: #000000;
        display: none;
    }
    .Author{
        background: #e43b3b;
        text-align: center;
        color: #ffffff;
    }
}
         /* Mobile Size*/
/*@media only screen and (min-width: 550px) and (max-width: 549px){*/
@media only screen and (min-width: 300px) and (max-width: 549px){
    .header h3{
        font-size: small;
    }
    .logo{
        width: min-content;
        height: fit-content;
    }
    .service-cell{
        /* background-color: #42b4b4;
        padding-left: 50px; */
        margin: 0px;
        align-content: center;
    }
    .service-cell h1{
        text-align: center;
        color: #000000;
        font-size: 40px;
    }
    .style-content{
        align-items: center;
    }
    .style-content h1{
        margin-top: auto;
        color: #fd1717;
        font-size: large;
    }
    .service-cell h2{
        text-align: center;
    }
    .background-image{
        max-height: 100px;
    }
    .client{
        background: #ffffff;
    }
    .client h4{
        color: black;
        font-size: large;
    }
    .image-cell{
        display: contents;
        width: auto;
        height: auto;
    }
    image{
        background: #fd1717;
    }
}